.footer-dark {
    padding: 50px 0;
    color: #f0f9ff;
    background-color: #E11D48;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.footer-dark h3 {
    margin-top: 0;
    margin-bottom: 12px;
    font-weight: bold;
    font-size: 22px;
}

.footer-dark .item.text p {
    opacity: 0.6;
    margin-bottom: 0;
}

.footer-dark .item.text {
    margin-bottom: 36px;
}

.footer-dark .item.social {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
}

.footer-dark .item.social>a {
    font-size: 20px;
    width: 36px;
    height: 36px;
    line-height: 36px;
    display: inline-block;
    text-align: center;
    border-radius: 50%;
    box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.4);
    margin: 0 8px;
    color: #fff;
    opacity: 0.75;
}

.footer-dark .item.social>a:hover {
    opacity: 1;
    scale: 1.25;
    transition: 0.3s ease-in-out;
}

.footer-dark .copyright {
    text-align: center;
    padding-top: 24px;
    opacity: 0.3;
    font-size: 13px;
    margin-bottom: 0;
}

@media (max-width: 767px) {
    .footer-dark .item.text {
        text-align: center;
    }

    .footer-dark .item.social {
        margin-top: 20px;
    }
}

@media (max-width: 991px) {
    .footer-dark .item.social {
        margin-top: 20px;
    }
}